.Badges__hero {
    width: 100%;
    background: #1B1B25;
    margin-bottom: 1rem;
    color: #ffffff;
    height: 100px;
    overflow: hidden;
  }

 
  .Badges__hero>.Badges__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Badges__container {
    width: 100%;
    max-width: 580px;
    margin: 0 auto;
    padding: 0 1rem;
  }
  
  .Badges_conf-logo {
    margin-bottom: 2rem;
    object-fit: cover;
  }
  
  .Badges__buttons {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
  }