@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
.BadgeList {
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 8px 8px;
  padding: 1rem 0;
  margin: 1rem 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.BadgeList__section-name {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  text-align: center;
}

.BadgeList__section-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  text-align: center;
}

.BadgeList__section-info p {
  font-size: 1.4rem;
}

.Badge__avatar {
  border-radius: 50%;
  width: 80px;
  height: 80px;
}

.Badge {
    background: #FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.10);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;
    height: 400px;
  }
  
  .Badge__header {
    padding: 0.5rem 0;
    height: 100px;
    background: #1B1B25;
    display: flex;
    justify-content: center;
  }

  .Badge__header img {
    object-fit: cover;
    object-position: 50% 41%;
  }
  
  .Badge__section-name {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
  }
  
  .Badge__section-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 0;
    background: #F4F4F7;
  }
  
  
  .Badge__avatar {
    border-radius: 50%;
    margin-right: 1rem;
    width: 120px;
    height: 120px;
  }
  
  .Badge__footer {
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #cc0000;
    font-weight: bold;
    font-style: italic;
  }
.Badges__hero {
    width: 100%;
    background: #1B1B25;
    margin-bottom: 1rem;
    color: #ffffff;
    height: 100px;
    overflow: hidden;
  }

 
  .Badges__hero>.Badges__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Badges__container {
    width: 100%;
    max-width: 580px;
    margin: 0 auto;
    padding: 0 1rem;
  }
  
  .Badges_conf-logo {
    margin-bottom: 2rem;
    object-fit: cover;
  }
  
  .Badges__buttons {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
  }
.BadgeNew__hero {
    width: 100%;
    background: #1B1B25;
    margin-bottom: 1rem;
    color: #ffffff;
    height: 100px;
    overflow: hidden;
}

.BadgeNew__hero img {
    object-fit: cover;
}



.Navbar {
    width: 100%;
    padding: 0;
    margin-bottom: 1rem;
    background-color: #1E90FF;
    
    overflow: hidden;
    
  }
  
.Navbar__brand {
    color: #ffffff;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    overflow: hidden;
    height: 85px;
  }

.Navbar__brand:hover {
    color: #ffffff;
    text-decoration: none;
}
  
.Navbar__brand-logo {
    position: relative;
    margin-right: 1rem;
    height: 80px;
    top: 0%;
}
.Footer {
  width: 100%;
  padding: 0;
  margin-top: 1rem;
  background-color: #1e90ff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
}

.Footer__created {
  color: #ffffff;
  font-size: 1rem;
}

.Footer__created a {
  text-decoration: underline;
}

.Footer__created a:hover {
  color: #ffffff;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #1E90FF;
  border-color: #1E90FF;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #1E90FF;
  border-color: #1E90FF;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #1E90FF;
  border-color: #1E90FF;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(74, 163, 218, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
